import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { useNavigate } from "react-router-dom";
import Profile from "../component/Profile";
import { getDocs, collection, query, where, setDoc, doc } from 'firebase/firestore'
import { authService, db } from '../fbase';


export default function AddCate() {

    const history = useNavigate();

    const [name, setName] = useState("");
    const [color, setColor] = useState("");
    const [img, setImg] = useState("https://ifh.cc/g/RxT0yX.png");
    const [nameList, setNameList] = useState([]);
    const [same, setSame] = useState(false);

    const [userUid, setUserUid] = useState("");
    const cate = collection(db, 'cate');
    const arr = [];
    const [newId, setId] = useState(0);


    useEffect(() => {
        //  
        async function getInfo() {
            // user uid  cate  
            await authService.onAuthStateChanged(user => {
                if (user) {
                    setUserUid(authService.currentUser.uid);
                }
                else { }
            })
            const myData = query(cate, where("uid", "==", userUid));
            const querySnapshot = await getDocs(myData);
            await querySnapshot.forEach((doc) => {
                arr.push(doc.data().name);
                setId(Number(doc.id));
            });
            setNameList(arr)
        }
        getInfo();
        /*
        axios.get(`http://localhost:3001/cate`)
            .then(res => {
                return res.data
            })
            .then(e => {
                setNameList(e.map((e) => {
                    return e.name;
                }))
            })*/
    }, [userUid]);

    //       
    useEffect(() => {
        for (let i = 0; i < nameList.length; i++) {
            (nameList[i] === name) ? setSame(true) : setSame(false);
        }
    }, [same, myName]);

    function myName(e) {
        setName(e.target.value);
        if (e.target.value === "") setName("");
    }

    function myColor(e) {
        setColor((e.target.value).replace("#", ""));
    }

    function myImg(e) {
        let reader = new FileReader();
        if (e.target.files[0]) {
            reader.readAsDataURL(e.target.files[0])
        }
        reader.onload = () => {
            const previewImgUrl = reader.result;
            setImg(previewImgUrl);
        }
    }

    async function addInfo() {
        //  
        if (name !== "" && color !== "" && same === false) {
            // id 
            await setDoc(doc(cate, String(newId + 1)), {
                name: name,
                color: color,
                img: img,
                uid: userUid
            });
            await alert(" !    ");
            await history(`/main`);
            /*axios.post(`http://localhost:3001/cate`, {
                name,
                color,
                img
            })
                .then(res => {
                    alert(" !    ");
                    history(`/main`);
                });*/
        }
        else if (same === true) alert("  !   ");
        else if ((name === "" && same === false) || (color === "" && same === false)) alert("ID Color  !");
    }

    return (
        <div>
            <AddCategory>
                <label>IMG <input type="file" accept="image/*" onChange={myImg} /></label>
                <label style={{ marginRight: "20px" }}>  <input style={{ height: "20px" }} type="text" maxLength="10" onChange={myName} /></label>
                <label>COLOR <input type="color" onChange={myColor} /></label>
            </AddCategory>
            <Blog $color={`#${color}`}>
                <Profile myname={name} img={img} /><br />
                <Div>
                    <Bookmark>
                        <span style={{ padding: "0 5px" }}>(  )</span>
                        <span style={{ borderLeft: "2px solid grey", padding: "0 5px", color: "grey" }}>  </span>
                    </Bookmark>
                </Div>
            </Blog>
            <Button>
                <button onClick={addInfo}></button>
            </Button>
        </div>
    );
}

//styled-components
const AddCategory = styled.div`
    background-color: lightgrey;
    padding: 20px;
    margin: 0px auto;
    width: 860px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom:10px;
`;

const Div = styled.div`
    margin: 40px;
    width: 600px;
    height: 150px;
`;

const Bookmark = styled.div`
    background-color: #fff;
    border-radius:5px;
`;

const Button = styled.p`
    display:flex;
    justify-content: center;
    align-items: center;
    margin-bottom:30px;
`;

const Blog = styled.div`
    margin:auto;
    margin-bottom: 50px;
    display:flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    width:900px;
    min-height:450px;
    border-radius: 20px;
    box-shadow: 8px 8px 5px rgba(133, 133, 133, 0.3);
    background-color: #e0f1ff;
    background-color:${props => props.$color};
`;